const app = initializeApp(firebaseConfig);
const db = getFirestore(app);

export interface HighScore {
  playerName: string;
  score: number;
}

export interface PlayHistory {
  score: number;
  date: string;
}

export const getHighScore = async (gridSize: number) => {
  const highScoreRef = doc(db, 'highScores', `${gridSize}x${gridSize}`);
  const docSnap = await getDoc(highScoreRef);
  if (docSnap.exists()) {
    return docSnap.data() as HighScore;
  } else {
    // Return a default high score if none exists
    return { playerName: 'None', score: 40 };
  }
};

export const setHighScore = async (gridSize: number, playerName: string, score: number) => {
  const highScoreRef = doc(db, 'highScores', `${gridSize}x${gridSize}`);
  await setDoc(highScoreRef, { playerName, score });
};

export const saveScoreToHistory = async (userId: string, gridSize: string, score: number) => {
  const historyRef = doc(db, 'playHistory', userId);
  const date = new Date().toISOString();
  await updateDoc(historyRef, {
    [`${gridSize}`]: arrayUnion({ score, date })
  }).catch(async (error) => {
    if (error.code === 'not-found') {
      await setDoc(historyRef, {
        [`${gridSize}`]: [{ score, date }]
      });
    } else {
      throw error;
    }
  });
};

export interface PlayHistoryEntry {
  score: number;
  date: string;
}

export const getPlayHistory = async (userId: string, gridSize: string): Promise<PlayHistory[]> => {
  const userHistoryRef = doc(db, 'playHistory', userId);
  const userHistoryDoc = await getDoc(userHistoryRef);
  if (userHistoryDoc.exists()) {
    const userData = userHistoryDoc.data();
    return userData[gridSize] || [];
  }
  return [];
};

export { app, db };
