import React, { useEffect, useState } from 'react';
import { useAuth0 } from '@auth0/auth0-react';
import Puzzle from './components/Puzzle';
import { getPlayHistory, PlayHistoryEntry } from './firebaseUtils'; // Import the function and interface to get play history

interface PlayHistory {
  '3x3': PlayHistoryEntry[];
  '4x4': PlayHistoryEntry[];
  '5x5': PlayHistoryEntry[];
}

const App: React.FC = () => {
  const { handleRedirectCallback, loginWithRedirect, logout, isAuthenticated, user, isLoading } = useAuth0();
  const [playHistory, setPlayHistory] = useState<PlayHistory | null>(null);
  const [showHistory, setShowHistory] = useState(false);
  const [loading, setLoading] = useState<boolean>(true);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    const processRedirect = async () => {
      if (window.location.search.includes('code=')) {
        try {
          await handleRedirectCallback();
        } catch (error) {
          console.error('Authentication Error:', error);
        }
      }
    };

    processRedirect();
  }, [handleRedirectCallback]);

  useEffect(() => {
    const fetchPlayHistory = async () => {
      if (isAuthenticated && user && user.sub) {
        setLoading(true);
        try {
          const userId = user.sub; // Assuming the user ID is stored in `sub`
          const history3x3 = await getPlayHistory(userId, '3x3');
          const history4x4 = await getPlayHistory(userId, '4x4');
          const history5x5 = await getPlayHistory(userId, '5x5');
          setPlayHistory({ '3x3': history3x3, '4x4': history4x4, '5x5': history5x5 });
        } catch (error) {
          setError('Failed to fetch play history.');
          console.error('Error fetching play history:', error);
        } finally {
          setLoading(false);
        }
      }
    };

    fetchPlayHistory();
  }, [isAuthenticated, user]);

  if (isLoading) {
    //return <Puzzle />;
    return <div>Loading...</div>;
  }

  const handleLogout = () => {
    const returnToUrl = `${window.location.origin}`;
    (logout as (options?: { returnTo?: string }) => void)({ returnTo: returnToUrl });
  };

  const toggleHistory = () => {
    setShowHistory(!showHistory);
  };

  return (
    <div className="App">
      {!isAuthenticated ? (
        <button onClick={() => loginWithRedirect()}>Log In</button>
      ) : (
        <div>
          <h2>Welcome, {user?.name}</h2>
          <button onClick={handleLogout}>Log Out</button>
          <button onClick={toggleHistory}>
            {showHistory ? 'Hide Play History' : 'Show Play History'}
          </button>
          {loading && <p>Loading play history...</p>}
          {error && <p>{error}</p>}
          {showHistory && playHistory && (
            <div>
              <h3>Play History</h3>
              <div>
                <h4>3x3</h4>
                <ul>
                  {playHistory['3x3'].map((entry, index) => (
                    <li key={index}>
                      Score: {entry.score}, Date: {new Date(entry.date).toLocaleDateString()}
                    </li>
                  ))}
                </ul>
              </div>
              <div>
                <h4>4x4</h4>
                <ul>
                  {playHistory['4x4'].map((entry, index) => (
                    <li key={index}>
                      Score: {entry.score}, Date: {new Date(entry.date).toLocaleDateString()}
                    </li>
                  ))}
                </ul>
              </div>
              <div>
                <h4>5x5</h4>
                <ul>
                  {playHistory['5x5'].map((entry, index) => (
                    <li key={index}>
                      Score: {entry.score}, Date: {new Date(entry.date).toLocaleDateString()}
                    </li>
                  ))}
                </ul>
              </div>
            </div>
          )}
        </div>
      )}
      {/* Place console logs within JSX or use development tools for debugging */}
      <div>
        <pre>{`isAuthenticated: ${isAuthenticated}`}</pre>
        <pre>{`user: ${JSON.stringify(user, null, 2)}`}</pre>
      </div>
      <Puzzle />
    </div>
  );
};

export default App;
