import React, { forwardRef } from 'react';

interface TileProps {
  index: number;
  position: { x: number; y: number };
  onClick: () => void;
  onDragStart: (event: React.DragEvent<HTMLDivElement>, index: number) => void;
  onDrop: (event: React.DragEvent<HTMLDivElement>, index: number) => void;
  onTouchStart: (event: React.TouchEvent<HTMLDivElement>, index: number) => void;
  onTouchEnd: (event: React.TouchEvent<HTMLDivElement>, index: number) => void;
  image: string;
  size: number;
  isEmpty: boolean;
  gridSize: number;
  tileRef: React.RefObject<HTMLDivElement>;
}

const Tile = forwardRef<HTMLDivElement, TileProps>(({
  tileRef,
  index,
  position,
  onClick,
  onDragStart,
  onDrop,
  onTouchStart,
  onTouchEnd,
  image,
  size,
  isEmpty,
  gridSize
}) => {
  // Calculate background position in pixels
  const backgroundPositionX = -position.x * size;
  const backgroundPositionY = -position.y * size;
  // Calculate the total size of the background image
  const backgroundSize = `${size * gridSize}px ${size * gridSize}px`;

  return (
    <div
      ref={tileRef} // Attach the ref
      role={isEmpty ? 'presentation' : 'button'}
      tabIndex={isEmpty ? -1 : 0}
      onClick={onClick}
      onKeyDown={(e) => {
        if (e.key === 'Enter' || e.key === ' ') {
          onClick();
        }
      }}
      draggable={!isEmpty}
      onDragStart={(e) => onDragStart(e, index)}
      onDrop={(e) => onDrop(e, index)}
      onDragOver={(e) => e.preventDefault()}
      onTouchStart={(e) => onTouchStart(e, index)}
      onTouchEnd={(e) => onTouchEnd(e, index)}
      style={{
        width: `${size}px`,
        height: `${size}px`,
        position: 'relative',
        backgroundImage: isEmpty ? 'none' : `url(${image})`,
        backgroundPosition: isEmpty ? 'none' : `${backgroundPositionX}px ${backgroundPositionY}px`,
        backgroundSize: backgroundSize,
        borderRadius: '5px',
        cursor: isEmpty ? 'default' : 'pointer',
        boxSizing: 'border-box',
        transition: 'transform 0.3s ease',
      }}
    >
      {!isEmpty && (
        <div
          style={{
            position: 'absolute',
            top: '5px',
            left: '5px',
            width: '20px',
            height: '20px',
            backgroundColor: 'white',
            color: 'black',
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            borderRadius: '3px',
            fontSize: '14px',
            fontWeight: 'bold',
            boxSizing: 'border-box',
            transition: 'transform 0.3s ease',
          }}
        >
          {index + 1}
        </div>
      )}
    </div>
  );
});

Tile.displayName = 'Tile';

export default Tile;
