import React from 'react';
import { Auth0Provider } from '@auth0/auth0-react';
import { useNavigate } from 'react-router-dom';

interface Auth0ProviderWithHistoryProps {
  children: React.ReactNode;
}

const Auth0ProviderWithHistory: React.FC<Auth0ProviderWithHistoryProps> = ({ children }) => {
  const navigate = useNavigate();

  const onRedirectCallback = (appState?: { returnTo?: string }) => {
    navigate(appState?.returnTo || '/sliding-puzzle-game');
  };

  return (
    <Auth0Provider
      domain="dev-bii7xwj0q8um5en2.jp.auth0.com"
      clientId="SYWWVGn0wBHpFg9doHhohvv6tqfCARCD"
      authorizationParams={{ redirect_uri: `${window.location.origin}/sliding-puzzle-game` }}
      onRedirectCallback={onRedirectCallback}
    >
      {children}
    </Auth0Provider>
  );
};

export default Auth0ProviderWithHistory;
